const mapWork = {
    validCoordinates(x = 0, y = 0) {
        return (x >= -50 && x <= 50 && y >= -50 && y <= 50);
    },
    safeCoordinate(c = 0) {
        return Math.floor(c < -50 ? -50 : c > 50 ? 50 : c);
    },
    safeId(id = 1) {
        return Math.floor(id < 1 ? 1 : id > 10201 ? 10201 : id);
    },
    getId(x = 0, y = 0) {
        return this.safeId((x + 50) * 101 + (y + 50 + 1));
    },
    getCoordinates(id = 1) {
        let w = this.safeId(id) - 1;
        let x = Math.floor(w / 101);
        let y = Math.floor(w - 101 * x);
        return { x: x - 50, y: y - 50 };
    }
}

export default mapWork;