import React from "react";
import Pure from "./Pure";
import "./Profile.scss";
import client from "./CLASSES/client";
import mapWork from "./Functions/mapWork";
import { Race } from "./CLASSES/troops";
import { Link, Navigate } from "react-router-dom";
import getParam from "./Functions/getParam";
import { Button } from "react-bootstrap";
import validators from "./Functions/validation";
import { openSnackBar } from "./Interfaces/SnackBar";

class Profile extends React.PureComponent {
    type() {
        let d = this.props.display;
        if (d === "edit") return <Edit />;
        return <View />;
    }
    render() {
        return <div id="Profile">{this.type()}</div>;
    }
}

class View extends Pure {
    state = {
        ready: false,
        name: "",
        rank: 0,
        race: "",
        allianceId: 0,
        allianceName: "",
        numberOfVillages: 0,
        population: 0,
        age: 0,
        gender: 0,
        address: "",
        villages: <></>,
        description: ["", ""],
        self: false,
        id: 0
    }
    onReady() {
        client
            .viewProfile(getParam("id", 0, true))
            .then(profile => {
                if (!this.componentIsReady) return;
                let villages = profile.villages.map((village, index) => {
                    let id = village[1];
                    let coord = mapWork.getCoordinates(id);
                    return <tr key={index}>
                        <td>
                            <Link to={"/View?id=" + id}>{village[0]}</Link>
                            {village[2] ? <span className="capital">(عاصمة)</span> : <></>}
                        </td>
                        <td>{village[3]}</td>
                        <td><Link to={"/Map?id=" + id}>({coord.x} , {coord.y})</Link></td>
                    </tr>
                });
                let gender = profile.gender;
                this.setState({
                    ready: true,
                    name: profile.name,
                    rank: profile.rank,
                    race: Race.Name(profile.race),
                    allianceId: profile.allianceId || 0,
                    allianceName: profile.allianceName || "-",
                    numberOfVillages: profile.numberOfVillages,
                    population: profile.population,
                    age: Math.floor((new Date() - new Date(profile.dob).getTime()) / 3.15576e+10),
                    gender: gender === 1 ? "ملك`" : gender === 2 ? "ملكة" : "-",
                    address: profile.address || "-",
                    villages: villages,
                    description: profile.description,
                    self: profile.self,
                    id: profile.id
                });
            })
    }
    showControls() {
        if (!this.state.self) return <></>;
        return <div className="player-controls">
            <Link to="/Profile" className="highlight">نظرة عامة</Link>&nbsp;|&nbsp;
            <Link to="/Profile/edit">من نحن</Link>&nbsp;|&nbsp;
            <Link to="/Profile/options">الخيارات</Link>&nbsp;|&nbsp;
            <Link to="/Profile/settings">الاعدادات</Link>
        </div>
    }
    render() {
        let player = this.state;
        if (!player.ready)
            return <></>;
        else
            return <>
                {this.showControls()}
                <table className="player-info table_collapsed">
                    <thead><tr><th colSpan={2}>{player.name}</th></tr></thead>
                    <tbody>
                        <tr>
                            <td className="player-data">
                                <table>
                                    <tbody>
                                        <tr><td>الرتبة:</td><td>{player.rank}</td></tr>
                                        <tr><td>القبيلة:</td><td>{player.race}</td></tr>
                                        <tr><td>التحالف:</td><td>dev...</td></tr>
                                        <tr><td>القرى:</td><td>{player.numberOfVillages}</td></tr>
                                        <tr><td>السكان:</td><td>{player.population}</td></tr>
                                        <tr><td>العمر:</td><td>{player.age}</td></tr>
                                        <tr><td>الجنس:</td><td>{player.gender}</td></tr>
                                        <tr><td>السكن:</td><td>{player.address}</td></tr>
                                        <tr><td colSpan={2}>{player.self ? <Link to="/Profile/edit">تعديل المعلومات الشخصية</Link> : <Link to={"/Messages/send?name=" + player.name}>إرسال رسالة</Link>}</td></tr>
                                    </tbody>
                                    <tfoot>
                                        <tr><td colSpan={2} className="description">{player.description[0]}Desc1</td></tr>
                                    </tfoot>
                                </table>
                            </td>
                            <td className="description">{player.description[1]}Desc2</td>
                        </tr>
                    </tbody>
                </table>
                <table className="player-villages table_collapsed">
                    <thead><tr><th colSpan={3}>القرى</th></tr></thead>
                    <tbody>
                        <tr>
                            <td>الاسم</td>
                            <td>السكان</td>
                            <td>الإحداثيات</td>
                        </tr>
                        {player.villages}
                    </tbody>
                </table>
            </>;
    }
}

class Edit extends Pure {
    state = {
        ready: false,
        redirect: false,
        name: "",
        dob: "",
        gender: 0,
        address: "",
        villageName: "",
        description: ["", ""],
        medals: [],
    }
    data = {
        dob: "",
        gender: 0,
        address: "",
        villageName: "",
        description: ["", ""]
    }

    onReady() {
        client
            .editProfile()
            .then(profile => {
                if (!this.componentIsReady) return;
                let d = this.data;
                this.setState({
                    ready: true,
                    name: profile.name,
                    dob: d.dob = profile.dob,
                    gender: d.gender = profile.gender,
                    address: d.address = profile.address,
                    villageName: d.villageName = profile.villageName,
                    description: d.description = profile.description,
                    medals: profile.medals,
                });
            });
    }
    actionHandler() {
        let d = this.data;
        let safeAddress = validators.safeText(d.address, 0, 24);
        let safeVillage = validators.safeText(d.villageName, 0, 24);
        let safeD1 = validators.safeText(d.description[0], 0, 512);
        let safeD2 = validators.safeText(d.description[1], 0, 512);
        if (!safeAddress.valid)
            return openSnackBar("السكن: " + safeAddress.result);
        if (!safeVillage.valid)
            return openSnackBar("أسم القرية: " + safeVillage.result);
        if (!safeD1.valid)
            return openSnackBar("مربع الوصف الأول: " + safeD1.result);
        if (!safeD2.valid)
            return openSnackBar("مربع الوصف الثاني: " + safeD2.result);
        let age = Math.floor((new Date() - new Date(d.dob).getTime()) / 3.15576e+10);
        if (age < 1 || age > 100)
            return openSnackBar("تاريخ الميلاد غير صحيح");
        client
            .saveProfile(this.data)
            .then(response => {
                if (!this.componentIsReady) return;
                console.log(response);
                if (response.done) {
                    openSnackBar("تم الحفظ");
                    this.setState({ redirect: true });
                } else
                    openSnackBar("يوجد خطأ, لم يتم الحفظ");
            });
    }
    render() {
        let player = this.state;
        if (!player.ready)
            return <></>;
        else if (player.redirect)
            return <Navigate to="/Profile" />
        else
            return <>
                <div className="player-controls">
                    <Link to="/Profile">نظرة عامة</Link>&nbsp;|&nbsp;
                    <Link to="/Profile/edit" className="highlight">من نحن</Link>&nbsp;|&nbsp;
                    <Link to="/Profile/options">الخيارات</Link>&nbsp;|&nbsp;
                    <Link to="/Profile/settings">الاعدادات</Link>
                </div>
                <table className="player-info profile-edit table_collapsed">
                    <thead><tr><th colSpan="3">{player.name}</th></tr></thead>
                    <tbody>
                        <tr>
                            <td>تاريخ الميلاد:</td>
                            <td><input onChange={e => { this.data.dob = e.target.value; }} type="date" defaultValue={player.dob} /></td>
                            <td rowSpan="0" className="second">
                                <div><textarea onChange={e => { this.data.description[1] = e.target.value; }} defaultValue={player.description[1]}></textarea></div>
                            </td>
                        </tr>
                        <tr>
                            <td>الجنس:</td>
                            <td>
                                <select onChange={e => { this.data.gender = e.target.value; }} defaultValue={player.gender}>
                                    <option value={0}>غير محدد</option>
                                    <option value={1}>ذكر</option>
                                    <option value={2}>أنثى</option>
                                </select>
                            </td>
                        </tr>
                        <tr><td>السكن:</td><td><input onChange={e => { this.data.address = e.target.value; }} type="text" defaultValue={player.address} /></td></tr>
                        <tr><td>أسم القرية:</td><td><input onChange={e => { this.data.villageName = e.target.value; }} type="text" defaultValue={player.villageName} /></td></tr>
                        <tr><td colSpan="2" style={{ width: "50%" }}><textarea onChange={e => { this.data.description[0] = e.target.value; }} ref={this.textarea} defaultValue={player.description[0]}></textarea></td></tr>
                    </tbody>
                </table>
                <Button size="sm" className="edit" onClick={() => this.actionHandler()} variant="dark">حفظ</Button>
            </>;
    }
}

export default Profile;